/* GENERAL BUTTON STYLING */
.btn-delete {
  padding: 4px 15px;
  background: #59da93;
  border: 3px solid #76ce9b;
  border-radius: 5px;
  color: #fff;
  display: block;
  font-size: initial;
  margin-bottom: 3px;
  position: relative;
}
.btn-delete:hover {
  color: #59da93;
  background: #fff;
}

